import * as yup from "yup";

export const registerSchema = yup.object({
  userName: yup.string().trim().required("Podaj nazwe uytkownika"),
  password: yup
    .string()
    .trim()
    .required("Podaj haso")
    .matches(
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/,
      "Haso powinno zawiera min. 8 znakw, zawiera mae i due litery, cyfr oraz znak specjalny"
    ),
  repeatPassword: yup
    .string()
    .trim()
    .required("Powtrz haso")
    .oneOf([yup.ref("password"), null], "Hasa musz by jednakowe"),
  email: yup
    .string()
    .trim()
    .required("Podaj adres email")
    .email("Nieprawidowy adres email"),
});
