import * as yup from "yup";

export const manageDogProfileSchema = yup.object({
  dogName: yup.string().trim().required("Podaj nazw psa"),
  race: yup.string().trim().required("Podaj ras psa"),
  age: yup
    .string()
    .trim()
    .required("Podaj wiek psa")
    .test("Is positive?", "Nieprawidowy wiek psa", (value) =>
      Boolean(value && +value > 0)
    ),
  weight: yup
    .string()
    .trim()
    .required("Podaj wag psa")
    .test("Is positive?", "Nieprawidowa waga psa", (value) =>
      Boolean(value && +value > 0)
    ),
});
