import * as yup from "yup";

export const editProfileSchema = yup.object({
  userName: yup.string().trim().required("Podaj nazwe uytkownika"),
  email: yup
    .string()
    .trim()
    .required("Podaj adres email")
    .email("Nieprawidowy adres email"),
  phoneNumber: yup
    .string()
    .trim()
    .matches(/^\d+$/, "Nieprawidowy numer telefonu")
    .length(9, "Nieprawidowy numer telefonu")
    .nullable(),
});
