import * as yup from "yup";

export const newPasswordSchema = yup.object({
  oldPassword: yup.string().trim().required("Podaj stare haso"),
  newPassword: yup
    .string()
    .trim()
    .required("Podaj nowe haso")
    .matches(
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/,
      "Haso powinno zawiera min. 8 znakw, zawiera mae i due litery, cyfr oraz znak specjalny"
    ),
  repeatNewPassword: yup
    .string()
    .trim()
    .required("Powtrz nowe haso")
    .oneOf([yup.ref("newPassword"), null], "Hasa musz by jednakowe"),
});
